/*
** Common Sections Styles
*/
section {
  position: relative;
}

img {
  max-width: 100%;
  border-radius: 5px 5px 0 0;
}

/*parallax*/
.s-parallax,
.cover-image,
.cover-background,
.texture-background {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  position: relative;
}

.cover-image {
  width: 100%;
  height: 100%;
}

.texture-background {
  background-image: url(../img/background-texture-cover.jpg);
}

.pattern-background:before {
  background-image: url(../img/patterns/pattern8.png);
}

@media (min-width: 768px) {
  .cover-image {
    display: block;
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    background-position: 50% 50%;
    z-index: 2;
  }
  .cover-image.s-cover-right {
    width: 50%;
    left: auto;
  }
  .cover-image.s-cover-left {
    width: 50%;
  }
  .cover-image > img {
    visibility: hidden;
  }
  .cover-image > a {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
  .cover-image + [class*='container'] [class*='col-'] > img,
  .cover-image + [class*='container'] [class*='col-'] > a:after,
  .cover-image + [class*='container'] [class*='col-'] > a > img {
    visibility: hidden;
  }
}

.fixed-background,
.s-parallax {
  background-attachment: fixed;
  background-position: 50% 0;
}

@media (max-width: 767px) {
  .s-parallax {
    -webkit-background-size: auto auto;
    -moz-background-size: auto auto;
    -o-background-size: auto auto;
    background-size: auto auto;
  }
}

/*additional overlay for parallax sections*/
.s-overlay:before {
  opacity: 0.8;
}

.s-overlay.ls:before {
  background-color: #ffffff;
  opacity: 0.93;
}

.s-overlay.ls.ms:before {
  background-color: #f2f2f2;
  opacity: 0.81;
}

.s-overlay.cs:before {
  background-color: #6f5499;
  opacity: 0.1;
}

.s-overlay.cs.cs2:before {
  background-color: #FC7D49;
}

.s-overlay.cs.cs3:before {
  background-color: #CF423C;
}

.s-overlay.ds:before {
  background-color: #20252b;
  opacity: 0.7;
}

.s-overlay.ds.ms:before {
  opacity: 0.90;
}

.s-muted:before {
  background-color: rgba(32, 37, 43, 0.1);
  opacity: 1;
}

.gradientradial-background {
  overflow: hidden;
}

.s-parallax > *,
.s-overlay > *,
.s-muted > *,
.s-overlay-diagonal-double > *,
.pattern-background > *,
.gradientradial-background > *,
.gradient-background > *,
.gradientvertical-background > *,
.gradientdarken-background > * {
  z-index: 4;
  position: relative;
}

.s-parallax:before,
.s-overlay:before,
.s-muted:before,
.s-overlay-diagonal-double:before,
.s-overlay-diagonal-double:after,
.pattern-background:before,
.gradient-background:before,
.gradientvertical-background:before,
.gradientdarken-background:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.s-muted:before,
.s-parallax:before {
  opacity: 0.8;
}

.s-overlay-diagonal {
  overflow: hidden;
  padding: 10px 0;
  background-clip: content-box;
}

.s-overlay-diagonal:before {
  width: 40%;
  height: 3000px;
  margin-left: 30%;
  top: 50%;
  left: 0;
  right: auto;
  bottom: auto;
  transform: translateY(-50%) rotate(-45deg);
}

@media (max-width: 1200px) {
  .s-overlay-diagonal:before {
    width: 70%;
    margin-left: 15%;
  }
}

.s-overlay-diagonal-double {
  overflow: hidden;
  padding: 10px 0;
}

.s-overlay-diagonal-double:before {
  background: linear-gradient(45deg, #6f5499 30%, transparent 30%);
  opacity: 0.6;
}

.s-overlay-diagonal-double:after {
  background: linear-gradient(45deg, transparent 70%, #6f5499 70%);
  opacity: 0.6;
}

.gradient-background:before {
  background: #6f5499;
  background: -moz-linear-gradient(left, #6f5499 0%, #fc7d49 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, #6f5499), color-stop(100%, #fc7d49));
  background: -webkit-linear-gradient(left, #6f5499 0%, #fc7d49 100%);
  background: -o-linear-gradient(left, #6f5499 0%, #fc7d49 100%);
  background: -ms-linear-gradient(left, #6f5499 0%, #fc7d49 100%);
  background: linear-gradient(to right, #6f5499 0%, #fc7d49 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$colorMain', endColorstr='$colorMain2', GradientType=1);
}

.gradientvertical-background:before {
  background: #6f5499;
  /* Old browsers */
  background: -moz-linear-gradient(top, #6f5499 0%, #fc7d49 100%);
  background: -webkit-linear-gradient(top, #6f5499 0%, #fc7d49 100%);
  background: -o-linear-gradient(top, #6f5499 0%, #fc7d49 100%);
  background: -ms-linear-gradient(top, #6f5499 0%, #fc7d49 100%);
  background: linear-gradient(to bottom, #6f5499 0%, #fc7d49 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$colorMain', endColorstr='$colorMain2', GradientType=0);
}

.gradientdarken-background:before {
  background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: -ms-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.4) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#00000000', GradientType=0);
}

.gradientradial-background:before {
  content: '';
  display: block;
  position: absolute;
  width: 1400px;
  height: 1400px;
  top: 50%;
  left: 50%;
  margin-top: -700px;
  margin-left: -700px;
  z-index: 2;
  background: -moz-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%);
  /* FF3.6+ */
  background: -webkit-gradient(radial, center center, 0, center center, 50%, color-stop(0%, rgba(255, 255, 255, 0.24)), color-stop(50%, rgba(255, 255, 255, 0)));
  /* Chrome,Safari4+ */
  background: -webkit-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%);
  /* Chrome10+,Safari5.1+ */
  background: -o-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%);
  /* Opera 12+ */
  background: -ms-radial-gradient(center, ellipse cover, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%);
  /* IE10+ */
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 50%);
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#33ffffff', endColorstr='#00ffffff', GradientType=1);
  /* IE6-9 fallback on horizontal gradient */
}

.s-overlay.gradientradial-background:before {
  width: 100vw;
  height: 100vw;
  margin-top: -50vw;
  margin-left: -50vw;
}

