  position: relative;
  text-transform: uppercase;
  margin: 0 auto;
  top: 25vh;
  width: 100vw;
  text-align: center;
  font-size: 40px;
  color: #20252b;
  opacity: .75;
  animation: pulse 2.5s linear infinite;
