  font-family: 'FontAwesome', sans-serif;
  font-size: 14px;
  line-height: 1;
  display: inline-block;
  width: 1em;
}

a.fa {
  font-size: 0;
  line-height: 0;
  text-align: center;
  margin-left: 8px;
  margin-right: 8px;
}

a.fa.bg-icon,
a.fa .tooltip, a.fa.color-bg-icon,
a.fa .tooltip, a.fa.border-icon {
  margin-left: 3px;
  margin-right: 3px;
}

a.fa:first-child {
  margin-left: 0;
}

a.fa:last-child {
  margin-right: 0;
}

a.border-icon,
a.bg-icon,
a.color-bg-icon {
  margin-bottom: 4px;
}

a.border-icon:before,
a.bg-icon:before,
a.color-bg-icon:before {
  width: 2.6em;
  height: 2.6em;
  line-height: 2.6em;
}

a[class*='fa-'].rounded-icon {
  -webkit-border-radius: 50%;
  border-radius: 50%;
